@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@200&display=swap);
body{background-color:#fff}a{text-decoration:underline;-webkit-text-decoration-color:purple;text-decoration-color:purple}p{font-family:"JetBrains Mono";font-weight:200;word-wrap:break-word}

.home-screen{position:fixed;left:0;height:100%;width:100%;background-color:#121212;z-index:-500}.home-screen a,.home-screen h1,.home-screen p{color:#fff}.dark a,.dark h1,.dark p{color:#fff}.App-footer .instructions{margin-right:16px;font-family:"JetBrains Mono";font-weight:800;background:#222 -webkit-gradient(linear,left top,right top,from(#222),to(#222),color-stop(.5,#fff)) 0 0 no-repeat;-webkit-background-size:150px;color:rgba(255,255,255,.3);-webkit-background-clip:text;-webkit-animation-name:shine;-webkit-animation-duration:12s;-webkit-animation-iteration-count:infinite;text-shadow:0 0 0 rgba(255,255,255,.5)}@-webkit-keyframes shine{0%,10%{background-position:-2000px}20%{background-position:top left}90%{background-position:top right}100%{background-position:2000px}}.particleContainer{z-index:-1;position:fixed;width:100%;height:100%;top:0}.content{display:inline-block;margin:0;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}
.aboutPage{position:absolute;left:0;background-color:#fff;width:100%;height:100%}.light a,.light h1,.light p{color:#000}.light .row{margin-top:120px}.light .mark{color:#000}
